import React, {Component, Fragment} from 'react';

class List extends Component {
    constructor(props){
        super(props);
    }
	
	del = (index) => {
		this.props.deltodo(index)
	}
	
	tog = (index) => {
		this.props.toggle(index)
	}

	rendertodos=(done) => {
		return this.props.todos.map((todo,index)=> {
			//console.log(todo);
			if(done == todo.done) {
				return <li key={index}>
							<input type="checkbox" 
									checked={done}
									onClick={this.tog.bind(this, index)}/>
							{/*<span dangerouslySetInnerHTML={{__html:this.props.todos[this.props.index].title}}></span>*/}
							<span> {todo.title}</span>
							<button onClick={this.del.bind(this, index)}>  删除 </button>
						</li>
			}
			
		})
	}
	
    render(){
		
		var arr1=this.props.todos.filter(item=>
			 !item.done
		)
		var arr2=this.props.todos.filter(item=>
			 item.done
		)
		//console.log(arr1);
		return 	<div>
					<h2>Doing  ( {arr1.length} )</h2>
					<ul>{
						this.rendertodos(false)
					}</ul>
					<h2>Done ( {arr2.length})</h2>
					<ul>{
						this.rendertodos(true)
					}</ul>
				</div>
    }
}

export default List