import React, { Component } from 'react'
//import INput from './INput'
import List from './List'
import Input from './Input1'

class Todolist extends Component {
    constructor() {
        super();
        this.state = {
            inpValue: "",
			todos:[{title:'123f', done:false},{title:'123true', done:true},{title:'123f', done:false}]
        }
    }
    
    componentDidMount() {
		
        //this.input.focus();
		let todos=localStorage.getItem('todos')
		if(todos) {
			this.setState({
				todos:JSON.parse(todos)
			})
		}
    }
	componentDidUpdate() {
		localStorage.setItem(
		'todos',JSON.stringify(this.state.todos)
		)
	}
	handleChange = (e) => {
	    console.log(e.target.value);
	    this.setState({ inpValue: e.target.value })
	}
	addtodo=()=> {
		//this.state.todos.unshift({title:this.state.inpValue})
		if(this.state.inpValue == '')
			return ;
		this.setState({todos:[
			{title:this.state.inpValue,done:false},
			...this.state.todos
		]})
		//输入完后每次清空
		this.state.inpValue = ""
	}
	deltodo = (index)=> {
		//console.log('del');
		//console.log(index);
		//法一
		//this.state.todos.splice(index,1)
		//this.setState({todos:this.state.todos})
		
		//法二
		//let newtodos = JSON.parse(JSON.stringify(this.state.todos))
		//newtodos.splice(index, 1)
		//this.setState({todos:newtodos})
		
		//法三
		this.setState({
			todos:this.state.todos.filter((item,idx)=>idx!==index)
		})
	}
	toggle=(index)=> {
		let newtodos = JSON.parse(JSON.stringify(this.state.todos))
		newtodos[index].done=!newtodos[index].done
		this.setState({
			todos:newtodos
		})
		//localStorage.setItem('todos', JSON.stringify(this.state.todos))
	
	}
	
    render() {
		//const {todos, inpValue} = this.state	
        return ( < div >
					<Input inpValue={this.state.inpValue} handleChange={this.handleChange} addtodo={this.addtodo}/>
					<List  todos={this.state.todos} deltodo={this.deltodo} toggle={this.toggle} />
				</div >
        )
    }
}


export default Todolist;